import Vue from 'vue';
import axios from 'axios';
import QS from 'qs';
import store from '../store/index';
import router from '@/router/index'

// 环境的切换
if (process.env.NODE_ENV == 'development') {
    axios.defaults.baseURL = 'http://192.168.38.100:8088';
} else if (process.env.NODE_ENV == 'debug') {
    axios.defaults.baseURL = 'http://192.168.38.100:8088';
} else if (process.env.NODE_ENV == 'production') {
    axios.defaults.baseURL = 'http://192.168.38.100:8088';
}

//设置请求超时
axios.defaults.timeout = 10000;

// axios.defaults.withCredentials = true;

//post请求头的设置
axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';

// 请求拦截器
axios.interceptors.request.use(
    config => {
        // 每次发送请求之前判断是否存在token，如果存在，则统一在http请求的header都加上token，不用每次请求都手动添加了
        // 即使本地存在token，也有可能token是过期的，所以在响应拦截器中要对返回状态进行判断
        const token = store.state.token;
        token && (config.headers.Authorization = token);
        return config;
    },
    error => {
        return Promise.error(error);
    })

// 响应拦截器
axios.interceptors.response.use(
    response => {
        if (response.status === 200) {
            //当前后端已拦截了401、403、500等错误信息，返回的是类似{"code":401,"msg":"请登录以后操作","data":null,"success":false}
            interceptHttpCode200Business(response);

            return Promise.resolve(response);
        } else {
            return Promise.reject(response);
        }
    },
    // 服务器状态码不是200的情况
    error => {
        if (error.response.status) {
            interceptHttpCodeAndBusinessCode(error.response.status,error.response.data.message);
            return Promise.reject(error.response);
        }
    }
);
/**
 * 处理错误码业务
 * @param {Integer} code [http status or 业务中返回的code]
 * @param {String} msg [消息提示文本]
 */
function interceptHttpCodeAndBusinessCode(code,msg) {
    switch (code) {
        // 401: 未登录
        // 未登录则跳转登录页面，并携带当前页面的路径
        // 在登录成功后返回当前页面，这一步需要在登录页操作。
        case 401:
            // 清除token
            store.commit('clearLogin');
            router.replace({
                path: '/signin',
                query: { redirect: router.currentRoute.fullPath }
            });
            break;
            // 403 token过期
            // 登录过期对用户进行提示
            // 清除本地token和清空vuex中token对象
            // 跳转登录页面
        case 403:
            Toast({
                message: '登录过期，请重新登录',
                duration: 1000,
                forbidClick: true
            });
            // 清除token
            store.commit('clearLogin');
            // 跳转登录页面，并将要浏览的页面fullPath传过去，登录成功后跳转需要访问的页面
            setTimeout(() => {
                router.replace({
                    path: '/signin',
                    query: {
                        redirect: router.currentRoute.fullPath
                    }
                });
            }, 1000);
            break;
            // 404请求不存在
        case 404:
            Toast({
                message: '网络请求不存在',
                duration: 1500,
                forbidClick: true
            });
            break;
            // 其他错误，直接抛出错误提示
        default:
            Toast({
                message: msg,
                duration: 1500,
                forbidClick: true
            });
        }
}

function interceptHttpCode200Business(res) {
    if (res.data.code === 401 || res.data.code === 403 || res.data.code === 404 || res.data.code === 500) {
        interceptHttpCodeAndBusinessCode(res.data.code,res.data.msg);
    }
}

/**
 * get方法，对应get请求
 * @param {String} url [请求的url地址]
 * @param {Object} params [请求时携带的参数]
 */
export function get(url, params) {
    return new Promise((resolve, reject) => {
        axios.get(url, {
            params: params,
            headers:{
                'Authorization': localStorage.getItem('Authorization')
            }
        }).then(res => {
            resolve(res.data);
        }).catch(err => {
            reject(err.data)
        })
    });
}

/**
 * post方法，对应post请求
 * @param {String} url [请求的url地址]
 * @param {Object} params [请求时携带的参数]
 */
export function post(url, params) {
    return new Promise((resolve, reject) => {
        axios.post(url, params,{headers: {
            'Authorization': localStorage.getItem('Authorization'),
        }})
            .then(res => {
                resolve(res.data);
            })
            .catch(err =>{
                reject(err.data)
            })
    });
}
